'use client';

import { useState, useRef } from 'react';
import { X, Send, Loader2, AlertCircle, Info, Upload, FileText } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Card, CardContent } from '@/components/ui/card';
import { Textarea } from '@/components/ui/textarea';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import {
  submitTransaction,
  submitBatchTransferEqual,
  submitBatchTransferDifferent,
} from '@/lib/web3';
import { ethers } from 'ethers';

// Common Tokens on Base Sepolia (Example Addresses)
const TOKEN_OPTIONS = [
  { name: 'USDC (Testnet)', address: '0x036CbD53842c5426634e7929541eC2318f3dCF7e' },
  { name: 'WETH', address: '0x4200000000000000000000000000000000000006' },
  { name: 'Custom Address', address: 'custom' },
];

interface SubmitTransactionModalProps {
  isOpen: boolean;
  onClose: () => void;
  controllerAddress: string;
}

export function SubmitTransactionModal({ isOpen, onClose, controllerAddress }: SubmitTransactionModalProps) {
  // Top Level Mode
  const [mode, setMode] = useState<'transfer' | 'custom'>('transfer');
  
  // Transfer Configuration
  const [txScope, setTxScope] = useState<'single' | 'batch'>('single');
  const [batchType, setBatchType] = useState<'equal' | 'different'>('equal');
  const [assetType, setAssetType] = useState<'eth' | 'token'>('eth');

  // Token State
  const [selectedToken, setSelectedToken] = useState<string>('');
  const [customTokenAddress, setCustomTokenAddress] = useState('');

  // Inputs
  const [recipients, setRecipients] = useState(''); 
  const [amounts, setAmounts] = useState('');       
  
  // Custom Data Inputs
  const [customTo, setCustomTo] = useState('');
  const [customValue, setCustomValue] = useState('0');
  const [customData, setCustomData] = useState('0x');

  const [isPending, setIsPending] = useState(false);
  const [error, setError] = useState<string | null>(null);
  
  // File Upload Ref
  const fileInputRef = useRef<HTMLInputElement>(null);

  if (!isOpen) return null;

  // --- Helpers ---

  const parseList = (str: string) => str.split(/[\n,]+/).map(s => s.trim()).filter(s => s !== '');

  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (event) => {
      const text = event.target?.result as string;
      if (!text) return;

      // Parse CSV: address,amount
      const lines = text.split(/\r?\n/);
      const addrList: string[] = [];
      const amtList: string[] = [];

      lines.forEach(line => {
        const [addr, amt] = line.split(',');
        if (addr && addr.trim()) addrList.push(addr.trim());
        if (amt && amt.trim()) amtList.push(amt.trim());
      });

      setRecipients(addrList.join('\n'));
      // Only set amounts if batch type allows specific amounts
      if (batchType === 'different') {
        setAmounts(amtList.join('\n'));
      } else if (amtList.length > 0) {
        // If equal batch, maybe take the first one or just ignore
        setAmounts(amtList[0]); 
      }
    };
    reader.readAsText(file);
    // Reset input
    e.target.value = '';
  };

  const handleSubmit = async () => {
    setError(null);
    setIsPending(true);

    try {
      if (mode === 'custom') {
        await submitTransaction(
          controllerAddress,
          customTo,
          customValue || '0',
          false,
          ethers.ZeroAddress,
          customData || '0x'
        );
      } else {
        const recipientList = parseList(recipients);
        const amountList = parseList(amounts);
        
        if (recipientList.length === 0) throw new Error("Recipient address is required");
        if (amountList.length === 0) throw new Error("Amount is required");
        
        const isToken = assetType === 'token';
        let targetToken = ethers.ZeroAddress;

        if (isToken) {
          targetToken = selectedToken === 'custom' ? customTokenAddress : selectedToken;
          if (!ethers.isAddress(targetToken)) throw new Error("Invalid Token Address");
        }

        if (txScope === 'single') {
          await submitTransaction(
            controllerAddress,
            recipientList[0],
            amountList[0],
            isToken,
            targetToken,
            '0x'
          );
        } else {
          // Batch
          if (batchType === 'equal') {
             await submitBatchTransferEqual(
               controllerAddress,
               targetToken,
               recipientList,
               amountList[0]
             );
          } else {
             if (recipientList.length !== amountList.length) {
               throw new Error(`Mismatch: ${recipientList.length} recipients vs ${amountList.length} amounts.`);
             }
             await submitBatchTransferDifferent(
               controllerAddress,
               targetToken,
               recipientList,
               amountList
             );
          }
        }
      }

      onClose();
      window.location.reload();
    } catch (err: any) {
      console.error(err);
      setError(err.message || 'Transaction failed');
    } finally {
      setIsPending(false);
    }
  };

  return (
    <div className="fixed inset-0 z-50 bg-black/50 flex items-center justify-center p-4 animate-in fade-in">
      <Card className="w-full max-w-lg border-border bg-card max-h-[90vh] overflow-y-auto">
        <div className="flex items-center justify-between p-6 border-b border-border sticky top-0 bg-card z-10">
          <h2 className="text-xl font-bold">New Proposal</h2>
          <button onClick={onClose} className="text-muted-foreground hover:text-foreground">
            <X className="h-5 w-5" />
          </button>
        </div>

        <CardContent className="p-6 space-y-6">
          <Tabs value={mode} onValueChange={(v) => setMode(v as 'transfer' | 'custom')}>
            <TabsList className="grid w-full grid-cols-2 mb-4">
              <TabsTrigger value="transfer">Fund Transfer</TabsTrigger>
              <TabsTrigger value="custom">Custom Action</TabsTrigger>
            </TabsList>

            {/* ================= TRANSFER TAB ================= */}
            <TabsContent value="transfer" className="space-y-6">
              
              {/* 1. Scope Selection */}
              <div className="flex gap-4 p-1 bg-muted rounded-lg">
                <Button 
                   variant={txScope === 'single' ? 'secondary' : 'ghost'} 
                   className="flex-1" 
                   onClick={() => setTxScope('single')}
                >
                   Single Transfer
                </Button>
                <Button 
                   variant={txScope === 'batch' ? 'secondary' : 'ghost'} 
                   className="flex-1" 
                   onClick={() => setTxScope('batch')}
                >
                   Batch Transfer
                </Button>
              </div>

              {/* 2. Asset Selection */}
              <div className="space-y-3">
                 <Label>Asset Type</Label>
                 <RadioGroup 
                    defaultValue="eth" 
                    value={assetType} 
                    onValueChange={(v) => setAssetType(v as 'eth' | 'token')}
                    className="flex gap-6"
                 >
                    <div className="flex items-center space-x-2">
                      <RadioGroupItem value="eth" id="r-eth" />
                      <Label htmlFor="r-eth">Native (ETH)</Label>
                    </div>
                    <div className="flex items-center space-x-2">
                      <RadioGroupItem value="token" id="r-token" />
                      <Label htmlFor="r-token">ERC20 Token</Label>
                    </div>
                 </RadioGroup>
              </div>

              {/* Token Selection (Conditional) */}
              {assetType === 'token' && (
                <div className="animate-in fade-in slide-in-from-top-2 space-y-3">
                  <Label>Select Token</Label>
                  <Select value={selectedToken} onValueChange={setSelectedToken}>
                    <SelectTrigger>
                      <SelectValue placeholder="Choose a token..." />
                    </SelectTrigger>
                    <SelectContent>
                      {TOKEN_OPTIONS.map((t) => (
                        <SelectItem key={t.address} value={t.address}>
                          {t.name}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>

                  {selectedToken === 'custom' && (
                    <div className="pt-1">
                       <Label className="text-xs text-muted-foreground">Contract Address</Label>
                       <Input 
                          placeholder="0x..." 
                          value={customTokenAddress} 
                          onChange={(e) => setCustomTokenAddress(e.target.value)} 
                          className="mt-1"
                       />
                    </div>
                  )}
                </div>
              )}

              {/* 3. Batch Type Selection (Conditional) */}
              {txScope === 'batch' && (
                <div className="p-4 border rounded-lg space-y-4 bg-muted/30">
                   <div className="flex items-center justify-between">
                      <Label className="text-primary">Batch Configuration</Label>
                      <Button 
                        variant="outline" 
                        size="sm" 
                        onClick={() => fileInputRef.current?.click()}
                        className="h-7 text-xs"
                      >
                        <Upload className="h-3 w-3 mr-2" /> Upload CSV
                      </Button>
                      <input 
                        type="file" 
                        ref={fileInputRef} 
                        className="hidden" 
                        accept=".csv,.txt" 
                        onChange={handleFileUpload}
                      />
                   </div>

                   <RadioGroup 
                      value={batchType} 
                      onValueChange={(v) => setBatchType(v as 'equal' | 'different')}
                      className="flex flex-col gap-2"
                   >
                      <div className="flex items-center space-x-2">
                        <RadioGroupItem value="equal" id="b-equal" />
                        <Label htmlFor="b-equal">Equal Amount (One amount for all)</Label>
                      </div>
                      <div className="flex items-center space-x-2">
                        <RadioGroupItem value="different" id="b-diff" />
                        <Label htmlFor="b-diff">Different Amounts (CSV: address,amount)</Label>
                      </div>
                   </RadioGroup>
                </div>
              )}

              {/* 4. Recipients Input */}
              <div>
                 <Label>
                    {txScope === 'single' ? 'Recipient Address' : 'Recipient List'}
                 </Label>
                 {txScope === 'single' ? (
                   <Input 
                     placeholder="0x..." 
                     value={recipients} 
                     onChange={(e) => setRecipients(e.target.value)}
                     className="mt-1.5"
                   />
                 ) : (
                   <div className="space-y-1 mt-1.5">
                      <Textarea 
                        placeholder={`0x123...\n0x456...`}
                        value={recipients}
                        onChange={(e) => setRecipients(e.target.value)}
                        className="h-24 font-mono text-xs"
                      />
                      <p className="text-xs text-muted-foreground flex items-center gap-1">
                        <Info className="h-3 w-3"/> Separate addresses by new line
                      </p>
                   </div>
                 )}
              </div>

              {/* 5. Amount Input */}
              <div>
                 <Label>
                    {(txScope === 'batch' && batchType === 'different') ? 'Amounts List' : 'Amount'}
                 </Label>
                 {(txScope === 'batch' && batchType === 'different') ? (
                    <div className="space-y-1 mt-1.5">
                      <Textarea 
                        placeholder={`1.5\n0.5`}
                        value={amounts}
                        onChange={(e) => setAmounts(e.target.value)}
                        className="h-24 font-mono text-xs"
                      />
                      <p className="text-xs text-muted-foreground flex items-center gap-1">
                        <Info className="h-3 w-3"/> Must match order of recipients
                      </p>
                   </div>
                 ) : (
                   <Input 
                     type="number" 
                     placeholder="0.00" 
                     value={amounts} 
                     onChange={(e) => setAmounts(e.target.value)}
                     className="mt-1.5"
                   />
                 )}
              </div>

            </TabsContent>

            {/* ================= CUSTOM TAB ================= */}
            <TabsContent value="custom" className="space-y-4">
              <div className="bg-orange-500/10 border border-orange-500/20 p-3 rounded-md text-xs text-orange-600 mb-4">
                 Advanced: Send raw transactions or call external contracts directly.
              </div>
              <div>
                <Label>Target Contract Address</Label>
                <Input placeholder="0x..." value={customTo} onChange={(e) => setCustomTo(e.target.value)} className="mt-1.5"/>
              </div>
              <div>
                <Label>Value (ETH)</Label>
                <Input type="number" value={customValue} onChange={(e) => setCustomValue(e.target.value)} className="mt-1.5"/>
              </div>
              <div>
                <Label>Calldata (Hex)</Label>
                <Textarea 
                  placeholder="0x..." 
                  value={customData} 
                  onChange={(e) => setCustomData(e.target.value)} 
                  className="mt-1.5 font-mono text-xs h-24"
                />
              </div>
            </TabsContent>
          </Tabs>

          {/* Error Display */}
          {error && (
            <div className="p-3 bg-red-500/10 border border-red-500/20 rounded text-sm text-red-500 flex gap-2 items-start">
              <AlertCircle className="h-4 w-4 shrink-0 mt-0.5"/>
              <span className="break-all">{error}</span>
            </div>
          )}

          {/* Footer Actions */}
          <div className="flex gap-3 pt-4 border-t border-border">
            <Button variant="outline" className="flex-1" onClick={onClose} disabled={isPending}>
              Cancel
            </Button>
            <Button className="flex-1" onClick={handleSubmit} disabled={isPending}>
              {isPending ? <Loader2 className="h-4 w-4 animate-spin" /> : <Send className="h-4 w-4 mr-2" />}
              Submit Proposal
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}